import {
  SET_ALL_TARGETS_BYTAGNAME,
  SET_ALL_TARGETS,
  SET_RECOMMEND_TARGETS,
  SET_OPTIONAL_TARGETS,
  ADD_OPTIONAL_TARGETS,
  REMOVE_OPTIONAL_TARGETS,
  SET_CHOOSED_TARGETS,
  ADD_CHOOSED_TARGETS,
  REMOVE_CHOOSED_TARGETS,
  UPDATE_CHOOSED_TARGETS,
  SET_PAGENITION_CURR,
  SET_PAGENITION_TOTAL,
  SET_USERGROUP,
  SET_PREVGROUP,
  SET_GROUPNAME,
  SET_USER_SCALE
} from './mutation-types' //  mutation 

export default {
  [SET_ALL_TARGETS_BYTAGNAME](state, payload) { // 
    if (Array.isArray(payload.data)) {
      state.allTargetsSearch = payload.data
    }
  },
  [SET_ALL_TARGETS](state, payload) { // 
    if (Array.isArray(payload.data)) {
      state.allTargets = payload.data
    }
  },
  [SET_RECOMMEND_TARGETS](state, payload) { // 
    if (Array.isArray(payload.data)) {
      state.recommendTargets = payload.data
    }
  },
  [SET_OPTIONAL_TARGETS](state, payload) { // 
    if (Array.isArray(payload.data)) {
      state.optionalTargets = payload.data
    }
  },
  [ADD_OPTIONAL_TARGETS](state, payload) { // 
    if (typeof payload.data === 'object') {
      state.optionalTargets.push(payload.data)
    }
  },
  [REMOVE_OPTIONAL_TARGETS](state, payload) { // 
    if (typeof payload.index === 'number') {
      state.optionalTargets.splice(payload.index, 1)
    }
  },
  [SET_CHOOSED_TARGETS](state, payload) { // 
    if (Array.isArray(payload.data)) {
      state.choosedTargets = payload.data
    }
  },
  [SET_USERGROUP](state, payload) { // 
    if (typeof payload.data === 'number') {
      state.userGroup = payload.data
    }
  },
  [SET_PREVGROUP](state, payload) { // 
    if (typeof payload.data === 'number') {
      state.prevGroup = payload.data
    }
  },
  [SET_GROUPNAME](state, payload) { // 
    if (typeof payload.data === 'string') {
      state.groupName = payload.data
    }
  },
  [ADD_CHOOSED_TARGETS](state, payload) { // 
    if (typeof payload.data === 'object') {
      state.choosedTargets.push(payload.data)
    }
  },
  [REMOVE_CHOOSED_TARGETS](state, payload) { // 
    if (typeof payload.index === 'number') {
      state.choosedTargets.splice(payload.index, 1)
    }
  },

  [UPDATE_CHOOSED_TARGETS](state, payload) { // 
    if (typeof payload.index === 'number') {
      // 
      //console.log(payload.index)
    }
  },

  [SET_PAGENITION_CURR](state, payload) { // 
    if (typeof payload.data === 'number') {
      state.current = payload.data
    }
  },
  [SET_PAGENITION_TOTAL](state, payload) { // 
    if (typeof payload.data === 'number') {
      state.allpage = payload.data
    }
  }
}


