import $ from 'jquery'
let html2canvas = require("./html2canvas.js")
let jsPDF = require("./jsPdf.debug.js")
var pdf =null
export function downPdf(){//pdf
      html2canvas(document.body, {
      onrendered:function(canvas) {
          var contentWidth = canvas.width;
          var contentHeight = canvas.height;
          //pdfhtmlcanvas;
          var pageHeight = contentWidth / 592.28 * 841.89;
          //pdfhtml
          var leftHeight = contentHeight;
          //pdf
          var position = 0;
          //a4[595.28,841.89]htmlcanvaspdf
          var imgWidth = 595.28;
          var imgHeight = 592.28/contentWidth * contentHeight;

          var pageData = canvas.toDataURL('image/jpeg', 1.0);

          pdf = new jsPDF('', 'pt', 'a4');
          //htmlpdf(841.89)
          //pdf
          if (leftHeight < pageHeight) {
              pdf.addImage(pageData, 'JPEG', 0, 0, imgWidth, imgHeight );
          } else {
              while(leftHeight > 0) {
                  pdf.addImage(pageData, 'JPEG', 0, position, imgWidth, imgHeight)
                  leftHeight -= pageHeight;
                  position -= 841.89;
                  //
                  if(leftHeight > 0) {
                      pdf.addPage();
                  }
              }
          }
          if (pdf!=null) {
            console.log('')
            pdf.save('result.pdf');
          }
          
      }
  })
}

var whatMode = 'dev' // 'dev'/'prod'
// objfake, url, succ, failmakeAction
var makeAction = function(obj) {
  if (whatMode === 'dev') {
    $.ajax({
      url: obj.fake,
      async: false,
      dataType: 'json',
      success: function (data) {
        obj.succ(data)
      },
      error: function(err) {
        obj.fail(err)
      }
    })
  } else if (whatMode === 'prod') {
     $.ajax({
      url: obj.url,
      dataType: 'json',
      async: false,
      type:obj.type,
      data:obj.data,
      success: function (data) {
        obj.succ(data)
      },
      error: function(err) {
        obj.fail(err)
      }
    })
  }
}
export{makeAction}



