import 'babel-polyfill'
import Vue from 'vue'
import Vuex from 'vuex'
import targetMutations from './targetMutations'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    allTargets: [], // 
    allTargetsSearch:[],//
    recommendTargets: [], // 
    optionalTargets: [], // 
    choosedTargets: [], // 
    userGroup: 0, // 
    prevGroup: 0, // 
    groupName: '', // 
    current:1,//
    allpage:1,//
  },
  getters: {
    optionalTargetNames: state => { //  fieldName 
      const result = state.optionalTargets.map(item => {
        return item.indexInfo.fieldName
      })
      return result
    },
    choosedTargetNames: state => { //  fieldName 
      const result = state.choosedTargets.map(item => {
        return item.indexInfo.fieldName
      })
      return result
    }
  },
  mutations: {
    ...targetMutations
  }
})

export default store


